<template>
    <div class="type-nav">
        <div class="container" @mouseleave="deleteColor()">
            <h2 class="all" @mouseenter="shownav">全部商品分类</h2>
            <nav class="nav">
                <a href="###">服装城</a>
                <a href="###">美妆馆</a>
                <a href="###">尚品汇超市</a>
                <a href="###">全球购</a>
                <a href="###">闪购</a>
                <a href="###">团购</a>
                <a href="###">有趣</a>
                <a href="###">秒杀</a>
            </nav>
            <transition name="sort" @mouseenter="shownav">
              <div class="sort" v-show="show">
                <div class="all-sort-list2" @click="goSearch">
                  <div class="item" v-for="(c1,index) in categoryList" :key="c1.categoryId" :class="{cur:curIndex==index}">
                    <h3 @mouseover="changeIndex(index)">
                       <!-- 一级分类 -->
                       <a :data-categoryName="c1.categoryName" :data-category1Id="c1.categoryId">{{c1.categoryName}}</a>
                     </h3>
                     <div class="item-list clearfix" :style="{display:curIndex==index?'block':'none'}">
                        <div class="subitem" v-for="c2 in c1.categoryChild" :key="c2.categoryId">
                          <dl class="fore">
                              <dt>
                                  <!-- 二级分类 -->
                                  <a :data-categoryName="c2.categoryName" :data-category2Id="c2.categoryId">{{c2.categoryName}}</a>
                              </dt>
                              <dd>
                                  <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                                      <!-- 三级分类 -->
                                      <a :data-categoryName="c3.categoryName" :data-category3Id="c3.categoryId">{{c3.categoryName}}</a>
                                      <!-- route-link在这里每次都会创建组件实例，会卡顿
                                        不建议使用 -->


                                        <!-- @click会导致有过多的回调函数
                                        不够理想，应该使用事件委派 -->

                                        <!-- 
                                          事件委派存在的问题：
                                              无法知道点击的是其中具体哪个标签
                                              如何获取参数
                                          -->
                                  </em>
                              </dd>
                          </dl>
                        </div>
                    </div>
                  </div>
                </div>
              </div>
            </transition>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import throttle from "lodash/throttle"
// 引入lodash的部分功能
// import _ from "lodash"引入全部功能
export default {
    name:"TypeNav",
    data(){
      return {
          curIndex:-1,
          show:false
      }
    },
    methods: {
      // changeColor(index){
      //     this.curIndex=index
      // },
      //ES6写法，没办法用防抖节流
      // 使用ES5的写法可以
      changeIndex:throttle(function(index){
          this.curIndex=index
      },100),
      deleteColor(){
        this.curIndex=-1
        if(this.$route.path!="/home"){
          this.show=false
        }
      },
      shownav(){
        this.show=true
      },
      goSearch(event){
          let element=event.target;
          // 自定义属性的使用
          let {categoryname,category1id,category2id,category3id}=element.dataset
          if(categoryname)//分类出a标签
          {
              let location ={name:"search"}

              let query={categoryName:categoryname}

              if(category1id){//区分级别
                  query.category1Id=category1id
              }
              else if(category2id){
                  query.category2Id=category2id
              }
              else if(category3id){
                  query.category3Id=category3id
              }
              // 整理参数
              location.query=query;
              if(this.$route.params){
                location.params=this.$route.params
              }
              this.$router.push(location,()=>{},()=>{})
          }
      }
    },
    computed:{
        ...mapState({
            // 这个state是大仓库的state
            categoryList:state=>state.home.categoryList
            
        })
    }
}
</script>

<style lang="css">
.type-nav {
  border-bottom: 2px solid #e1251b;
}
.type-nav .container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  position: relative;
}
.type-nav .container .all {
  width: 210px;
  height: 45px;
  background-color: #e1251b;
  line-height: 45px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.type-nav .container .nav a {
  height: 45px;
  margin: 0 22px;
  line-height: 45px;
  font-size: 16px;
  color: #333;
}



.type-nav .container .sort {
  left: 0;
  top: 45px;
  width: 210px;
  height: 461px;
  position: absolute;
  background: #fafafa;
  z-index: 999;
}
.type-nav .container .sort-enter{
  height:0px;
}

.type-nav .container .sort-enter-to{
  height:461px;
}
.type-nav .container .sort-enter-active{
  transition: all .5s linear;
}



/* .type-nav .container .sort .all-sort-list2 .item:hover {
    background-color: skyblue;
} */
/* 使用函数实现背景色随鼠标改变，学习防抖与节流 */
.cur{
    background-color: skyblue;
}
.type-nav .container .sort .all-sort-list2 .item h3 {
  line-height: 30px;
  font-size: 14px;
  font-weight: 400;
  overflow: hidden;
  padding: 0 20px;
  margin: 0;
}
.type-nav .container .sort .all-sort-list2 .item h3 a {
  color: #333;
}
.type-nav .container .sort .all-sort-list2 .item .item-list {
  /* display: none; */
  position: absolute;
  width: 734px;
  min-height: 460px;
  background: #f7f7f7;
  left: 210px;
  border: 1px solid #ddd;
  top: 0;
  z-index: 9999 !important;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem {
  float: left;
  width: 650px;
  padding: 0 4px 0 8px;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl {
  border-top: 1px solid #eee;
  padding: 6px 0;
  overflow: hidden;
  zoom: 1;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl.fore {
  border-top: 0;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dt {
  float: left;
  width: 54px;
  line-height: 22px;
  text-align: right;
  padding: 3px 6px 0 0;
  font-weight: 700;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd {
  float: left;
  width: 415px;
  padding: 3px 0 0;
  overflow: hidden;
}
.type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd em {
  float: left;
  height: 14px;
  line-height: 14px;
  padding: 0 8px;
  margin-top: 5px;
  border-left: 1px solid #ccc;
}
/* .type-nav .container .sort .all-sort-list2 .item:hover .item-list {
  display: block;
} */
</style>